@charset "UTF-8";
.about-container {
  margin-right: 60px;
  width: 50%; }
  .about-container:last-child {
    margin-right: 0; }

.advantages {
  list-style: none; }

.advantages-link {
  font-size: 14px;
  color: #292929;
  margin-left: 20px; }

.header {
  position: absolute;
  z-index: 2;
  width: 100%;
  top: 0;
  min-width: 1170px; }

.nav-wrap {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 40px; }

.nav-menu--link {
  text-decoration: none;
  padding: 10px 0;
  margin: 0 20px;
  color: #fff;
  transition: all 0.3s ease; }
  .nav-menu--link:hover {
    border-bottom: 1px solid #277abf;
    color: #277abf; }

.nav-menu {
  text-decoration: none;
  padding: 10px 0;
  margin: 0 20px;
  color: #fff;
  transition: all 0.3s ease; }
  .nav-menu:hover {
    color: #277abf; }

.second-phone {
  margin: 0;
  padding: 0;
  padding-left: 20px;
  border-left: 2px solid #fff; }

.order-btn {
  padding: 16px 20px;
  background-color: #007BC2;
  transition: all 0.3s ease; }
  .order-btn:hover {
    color: #277abf;
    background-color: #fff; }

.header-slider {
  position: relative; }

.slide {
  position: relative;
  margin-top: -40px;
  height: 100vh;
  background-size: cover; }
  .slide:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7); }

.slide1 {
  background-image: url("../../img/bg1.jpg"); }

.slide2 {
  background-image: url("../../img/bg2.jpg"); }

.slide3 {
  background-image: url("../../img/abbg1.jpg"); }

.slide-inner {
  position: absolute;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.head {
  color: #fff; }

.product-slider-wrap {
  display: flex;
  margin: 20px 100px;
  justify-content: space-around; }

.product-slide {
  background-color: #242424;
  position: relative; }

.product-link {
  width: 20%;
  margin: 0 10px;
  overflow: hidden; }
  .product-link:hover .new-car-img {
    transform: scale(1.1); }

.new-car-img {
  width: 100%;
  height: 100%;
  transition: all 0.3s ease; }

.item {
  overflow: hidden;
  text-align: center; }

.item-img {
  width: 100%;
  height: 100%; }

.item-discr {
  color: #fff;
  padding: 20px; }

.item-head {
  margin: 0 0 20px 0;
  padding: 0; }

.par-list-item {
  font-size: 14px;
  line-height: 20px; }

.parametr-list {
  list-style: none;
  margin: 0;
  padding: 0; }

.cleare-box {
  position: absolute;
  bottom: 20px;
  right: 20px;
  text-align: center;
  background-color: #007BC2;
  padding: 15px 5px; }

.cleare-box--sp {
  display: block;
  color: #fff; }

.precent {
  font-weight: bold;
  font-size: 18px; }

.cleared {
  font-size: 8px; }

.price {
  margin-top: 20px;
  font-size: 24px;
  color: #277abf;
  font-weight: bold; }

.item-sold {
  position: relative;
  font-size: 40px;
  letter-spacing: 1px;
  color: rgba(255, 255, 255, 0.5);
  transition: all 0.3s ease; }
  .item-sold:after {
    content: 'ПРОДАНО';
    position: absolute;
    z-index: 3;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.sold:hover .item {
  color: rgba(255, 255, 255, 0.9); }

.sold:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2; }

.about-wrap {
  display: flex;
  width: 60%;
  margin: 0 auto;
  /*justify-content: space-between;*/ }

.about-logo {
  padding-right: 50px; }

.about-text-container {
  width: 70%;
  padding-left: 50px;
  border-left: 1px solid #D9D9D9; }

.about-text {
  line-height: 24px;
  color: #292929; }

.sign {
  width: 10%;
  margin: 39px auto 0 auto; }

.about {
  background-color: #F0F0F0;
  padding: 80px 0; }

.info-section-wrap {
  display: flex;
  margin: 80px auto;
  width: 1170px;
  justify-content: space-around; }

.info-item {
  display: flex;
  width: 33.33%; }

.text-wrap {
  margin: 0 20px; }

.sign-img {
  opacity: 0.7; }

/****************FEEDBACK****************/
.feedback-section {
  background-color: #F0F0F0;
  text-align: center; }

.feedback-wrap {
  position: relative;
  padding: 80px 0;
  max-width: 700px;
  margin: 0 auto; }

.know-price-link {
  margin: 10px 0 20px 0;
  display: block;
  text-decoration: none;
  color: #9E9E9E; }

.feedback-head {
  font-size: 35px;
  font-weight: 100;
  margin: 0; }

.feedback-slider {
  position: relative; }

.person-img {
  margin: 0 auto; }

.person-discr {
  display: block; }

.person-name {
  font-weight: bold;
  font-size: 24px;
  margin: 20px 0; }

.person-feedback {
  color: #252626;
  font-size: 16px;
  max-width: 400px;
  margin: 0 auto; }

.arrow {
  position: absolute;
  font-size: 40px;
  color: #808080;
  cursor: pointer;
  transition: all 0.3s ease; }
  .arrow:hover {
    color: #007BC2; }

.ar-prew {
  left: 0;
  top: 50%; }

.ar-next {
  right: 0;
  top: 50%; }

/*********FOOTER********/
.footer {
  background-image: url("../../img/ft-bg.jpg");
  background-size: cover; }

.footer-wrap {
  width: 1170px;
  margin: 0 auto;
  padding: 80px 0;
  display: flex;
  justify-content: space-around;
  /*align-items: center;*/ }

.footer-nav-list {
  list-style: none;
  margin: 0; }

.footer-nav-link {
  padding: 10px 0; }

.phones {
  position: relative; }
  .phones:before {
    position: absolute;
    content: '';
    width: 25px;
    height: 25px;
    background-image: url("../../img/phone.png");
    left: -35px;
    top: 50%;
    transform: translateY(-50%); }

.email {
  position: relative;
  margin: 30px 0; }
  .email:before {
    position: absolute;
    content: '';
    width: 25px;
    height: 18px;
    background-image: url("../../img/mail.png");
    left: -35px;
    top: 50%;
    transform: translateY(-50%); }

.skype {
  position: relative; }
  .skype:before {
    position: absolute;
    content: '';
    width: 25px;
    height: 25px;
    background-image: url("../../img/skype.png");
    left: -35px;
    top: 50%;
    transform: translateY(-50%); }

.phone {
  display: block;
  margin: 5px 0;
  color: #fff; }

.ft-btn {
  color: #fff; }

.hover-effect {
  transition: all 0.3s ease;
  color: #fff; }
  .hover-effect:hover {
    color: #007BC2; }

.call-back {
  margin-top: 20px; }

@media screen and (max-width: 1366px) {
  .about-wrap {
    width: 80%; }
  .product-slider-wrap {
    margin: 20px 10px; } }

* {
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
  font-family: 'Arial', sans-serif; }
